import React, { Component } from 'react'
import './player.less';

class player extends Component {
    constructor(props) {
        super(props)
        this.state = {
            isdisabled: true,
            value: ''
        }
    }
    render() {
        var isdisabled = this.state.isdisabled
        let value = this.state.value
        return (
            <div className="player">
                <p>Players</p>
                <ul className="player-ul">
                    <li>
                        <h3>Player One</h3>
                        <form>
                            <input type="text" placeholder="github username" value={value} onChange={this.changeEvt.bind(this)} />
                            <input type="submit" value="submit" disabled={isdisabled} />
                        </form>
                    </li>
                    <li>
                        <h3>Player Two</h3>
                        <form>
                            <input type="text" placeholder="github username" />
                            <input type="submit" value="submit" disabled={isdisabled} />
                        </form>
                    </li>
                </ul>
                <hr />
            </div>
        )
    }
    changeEvt(e) {
        this.setState({
            value: e.target.value
        })
        if (e.target.value !== '') {
            this.setState({
                isdisabled: false
            })
        } else {
            this.setState({
                isdisabled: true
            })
        }
    }

}
export default player
